import { OnInit, OnChanges, Component } from "@angular/core";
import { ActivatedRoute } from '@angular/router';

import { Material } from "./material";
import { MaterialService } from "./material.service";
import { Subscription } from "rxjs";

@Component({
  templateUrl: './display-material.component.html',
  styleUrls: ['./display-material.component.css']
})


export class DisplayMaterialComponent  {
  _id: number;

  public material: Material | undefined;
  private subscription: Subscription;

  constructor(
    private router: ActivatedRoute,
    private materialService: MaterialService) {
    this.subscription = this.router.params.subscribe(params => this.id = params['id']);
  }


  set id(id: number) {
    this._id = id;
    this.getMaterialForCategoryId();
  }

  get id() {
    return this._id;
  }

  getMaterialForCategoryId() {
    this.materialService.getMaterials().subscribe(
      materials => {
        this.material = materials.find(
          (temp: Material) => {
            return Number(temp.categoryId) == this.id;
          });
      }
    );
  }

  
}
